@import url(https://fonts.googleapis.com/css?family=Ubuntu+Mono:400,700);
body {
  margin: 0;
  padding: 0;
  max-width: 1300px;
  margin: 0 auto;
  background: #E0E1DD;
  font-family: 'Ubuntu Mono', monospace;
}


.App {
  margin-top: 40px;
}

h1 {
  letter-spacing: 3px;
}


/* github */
.github {
  font-size: 13px;
  margin: 20px;
  margin-top: 110px;
  letter-spacing: 2px;
}
.github i {
  padding: 5px;
  color: black;
}
.github i:hover {
  color: #b0a8a2;
}
#duration {
  margin-top: 90px;
  margin-bottom: 80px;
}

.row {
  margin-bottom: 20px;
}

h4 {
  margin: 15px;
  font-weight: normal;
  text-decoration: underline;
  letter-spacing: 1px;
}
.buttons-column > button {
  margin: 0 15px;
  letter-spacing: 2px;
}
